</template>
<script>
const axios = require('axios')
  import { Indicator,Toast,Popup,Picker } from 'mint-ui'
  import threeLevelAddress from '../pca-code.json'
export default {
  data: function () {
    return {
      myAddressSlots: [
　　　　　　　　{
　　　　　　　　　　flex: 1,
　　　　　　　　　　defaultIndex: 1,
　　　　　　　　　　values: this.getProvinceArr(), //省份数组
　　　　　　　　　　className: 'slot1',
　　　　　　　　　　textAlign: 'center'
　　　　　　　　}, {
　　　　　　　　　　divider: true,
　　　　　　　　　　content: '-',
　　　　　　　　　　className: 'slot2'
　　　　　　　　}, {
　　　　　　　　　　flex: 1,
　　　　　　　　　　values: [],
　　　　　　　　　　className: 'slot3',
　　　　　　　　　　textAlign: 'center'
　　　　　　　　},{
　　　　　　　　　　divider: true,
　　　　　　　　　　content: '-',
　　　　　　　　　　className: 'slot4'
　　　　　　　　},{
　　　　　　　　　　flex: 1,
　　　　　　　　　　values: [],
　　　　　　　　　　className: 'slot5',
　　　　　　　　　　textAlign: 'center'
　　　　　　　　}
　　　　　　],
      popupVisible:false,
      selectValue: '',
      checkShows: true,
      username: '',
      idcard: '',
      nation: '',
      phone: '',
      work: '',
      workingYears: '',
      unitname: '',
      identifier: '',
      industry: '全民',
      receipt: '',
      checks: false,
      gender: '男',
      age: '',
      selected: '1',
      culture: '大专以下', // 文化程度
      actions: [{ name: '拍照',
        method: function () {
          console.log('1')
        } }, { name: '从相册选取',
        method: function () {
          console.log('2')
        } }],
        city:'选择体检城市',
        carType:'',
        myAddressProvince:'',
        myAddressCity:'',
        myAddresscounty:'',
        regionInit:false
    }
  },
  methods: {
    addressChange(picker, values) {
// 　　　　　　if(threeLevelAddress[values[0]]){ //这个判断类似于v-if的效果（可以不加，但是vue会报错，很不爽）
　　　　　　　　picker.setSlotValues(1,this.getCityArr(values[0]["name"])); // Object.keys()会返回一个数组，当前省的数组
　　　　　　　　picker.setSlotValues(2,this.getCountyArr(values[0]["name"], values[1]["name"])); // 区/县数据就是一个数组
// 　　　　　　　　this.myAddressProvince = values[0];
// 　　　　　　　　this.myAddressCity = values[1];
// 　　　　　　　　this.myAddresscounty = values[2];
// 　　　　　　}
            this.city = values[0]["name"] + values[1]["name"] + values[2]["name"]
        
　　　　},
    //遍历json，返回省级对象数组
      getProvinceArr() {
        let provinceArr = [];
        threeLevelAddress.forEach(function (item) {
          let obj = {};
          obj.name = item.name;
          obj.code = item.code;
          provinceArr.push(obj);
        });
        return provinceArr;
      },
      //遍历json，返回市级对象数组
      getCityArr(province) {
        // console.log("省：" + province);
        let cityArr = [];
        threeLevelAddress.forEach(function (item) {
          if (item.name === province) {
            item.children.forEach(function (args) {
              let obj = {};
              obj.name = args.name;
              obj.code = args.code;
              cityArr.push(obj);
            });
          }
        });
        return cityArr;
      },//遍历json，返回县级对象数组
      getCountyArr(province,city){
        let countyArr = [];
        threeLevelAddress.forEach(function(item){
          if (item.name === province){
            item.children.forEach(function (args) {
              if (args.name === city){
                args.children.forEach(function (param) {
                  let obj = {};
                  obj.name=param.name;
                  obj.code=param.code;
                  countyArr.push(obj);
                })
              }
            });
          }
        });
        // console.log(countyArr);
        return countyArr;
      },
    openCity(){
      this.popupVisible = true
      this.regionInit = true
    },
    check () {
      if (this.checkShows == false) {
        this.checkShows = true
      } else {
        this.checkShows = false
      }
    },
    appoint () {
      if(this.username == ''){
        Toast({
          message: '请填写姓名',
          position: 'middle',
          duration: 3000
        })
      }else if(this.idcard.length != 18){
        Toast({
          message: '请核对身份证号码',
          position: 'middle',
          duration: 3000
        })
      }else if(this.phone.length != 11){
        Toast({
          message: '请核对手机号码',
          position: 'middle',
          duration: 3000
        })
      }else if(this.city==''){
        Toast({
          message: '请选择体检城市',
          position: 'middle',
          duration: 3000
        })
      }else if(this.carType==''){
        Toast({
          message: '请输入准驾车型',
          position: 'middle',
          duration: 3000
        })
      }else{
        Indicator.open('加载中...')
        // axios({
        //   method: 'post',
        //   url: 'https://www.photo2030.com/webapi/api/Reserve/Reserve',
        //   data: {
        //     openID: '',
        //     HospitalID: this.selectValue.HospitalID,
        //     DateID: this.selectValue.ID,
        //     PictureID: '',
        //     Date: this.selectValue.Date,
        //     Name: this.username,
        //     IDCardNo: this.idcard,
        //     Sex: this.gender,
        //     Age: this.age,
        //     Nation: this.nation,
        //     Schooling: this.culture,
        //     UnitProperty: this.industry,
        //     UnitName: this.unitname,
        //     WorkType: this.work,
        //     WorkTime: this.workingYears,
        //     PhoneNo: this.phone,
        //     Token: 'B68DE9D1F8584D7099BCE47144D0351C'
        //   }
        // }).then(res => {
        //   Indicator.close()
        //   if(res.data.IsSuccess){
        //     this.$router.push({
        //     	path: '/result',
        //     	query: {Result:res.data}
        //     })
        //   }else{
        //     Toast({
        //       message: res.data.Message,
        //       position: 'middle',
        //       duration: 3000
        //     })
        //   }
        // }).catch(error => {
        //   Indicator.close()
        //   Toast({
        //     message: '服务器错误',
        //     position: 'middle',
        //     duration: 3000
        //   })
        // })
        // Indicator.close()
        this.$router.push({
            	path: '/result'
            })
            
      }
    }
  },
  created(){
    // this.selectValue = this.$route.query.selectValue
  },
  mounted(){
    this.$nextTick(() => {
      //vue里面全部加载好了再执行的函数 （类似于setTimeout）
      this.myAddressSlots[0].defaultIndex = 0;
      // 这里的值需要和 data里面 defaultIndex 的值不一样才能够初始化
      //因为我没有看过源码（我猜测是因为数据没有改变，不会触发更新）
    });
  }
}
</script>

<style scoped>
	.bigbox {
		width: 100%;
	}

	.box {
		background: rgb(248, 248, 248);
	}

	.mint-header {
