		display: block;
		line-height: 0.5rem;
	}

	.check-box {
		position: fixed;
		bottom: 0rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		align-items: center;
		width: 100%;
	}

	.select>div {
		width: 100%;
		display: flex;
		padding-left: 1.5rem;
	}
	.check-boxs {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: -0.05rem;
		padding: 0.3rem 0rem 2rem 0rem;
	}

	.check-boxs image {
		width: 0.34rem;
		height: 0.3rem;
	}

	.check-boxs .check-text {
		display: flex;
		font-size: 0.23rem;
